body {
  min-width: 320px;
  margin: 0 auto;
  min-height: 100%;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  background-color: #f2f2f2;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.main-container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-container {
    min-width: 768px;
    margin: 0 auto;
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .main-container {
    width: 1200px;
    margin: 0 auto;
    padding: 0;
    box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    min-width: 768px;
    padding: 0 60px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .container--form {
    padding: 0 120px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.image {
  width: 100%;
  height: auto;
  margin: 0;
}

.picture {
  line-height: 0;
}

@media (max-width: 767px) {
  .br-none-for-mobile {
    display: none;
  }
}

@media (min-width: 768px) {
  .br-none-for-tablet {
    display: none;
  }
}

@media (min-width: 1200px) {
  .br-none-for-desktop {
    display: none;
  }
}

.pristine-form--error {
  padding: 5px 10px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  background-color: #f16e6e;
  color: #000000;
}

@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans-400.woff2") format("woff2"), url("../fonts/ptsans-400.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-400.woff2") format("woff2"), url("../fonts/roboto-400.woff") format("woff");
  font-display: swap;
}
/*bold font*/
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsans-700.woff2") format("woff2"), url("../fonts/ptsans-700.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-700.woff2") format("woff2"), url("../fonts/roboto-700.woff") format("woff");
