  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}

.main-footer__tag-wrapper {
  padding: 38px 0 34px;
}
@media (min-width: 768px) {
  .main-footer__tag-wrapper {
    padding: 0;
  }
}

.main-footer__social {
  padding-bottom: 40px;
}
@media (min-width: 768px) {
  .main-footer__social {
    padding: 0;
    margin-right: -11px;
  }
}
@media (min-width: 1200px) {
  .main-footer__social {
    margin-right: 0;
  }
}

.main-footer__html-academy {
  padding: 0 0 23px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .main-footer__html-academy {
    padding: 0;
  }
}

.main-header {
  position: relative;
  display: flex;
  justify-content: center;
  min-height: 56px;
}
@media (min-width: 768px) {
  .main-header {
    padding: 0;
  }
}
.main-header--nojs {
  flex-direction: column;
  align-items: center;
}

.main-header__logo {
  position: absolute;
  width: 185px;
  height: 96px;
  justify-self: center;
  z-index: 2;
}
.main-header__logo--nojs {
  bottom: -96px;
}

.main-header__toggle {
  position: absolute;
  content: "";
  top: 16px;
  right: 25px;
  width: 22.9px;
  height: 22px;
  border: none;
  background-color: transparent;
  fill: #81b3d2;
  opacity: 1;
  z-index: 4;
}
.main-header__toggle:hover {
  opacity: 60%;
}
.main-header__toggle:active {
  opacity: 30%;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle--opened {
  right: 25px;
  width: 22.2px;
  height: 22.2px;
  fill: #ffffff;
}
.main-header__toggle--nojs {
  display: none;
}

.main-header__icon--cross {
  display: none;
}

.main-header__toggle--opened .main-header__icon--cross {
  display: block;
}

.main-header__toggle--opened .main-header__icon--burger {
  display: none;
}

.main-navigation {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  font-size: 18px;
}
.main-navigation--closed {
  display: none;
}
@media (min-width: 768px) {
  .main-navigation--closed {
    position: static;
    display: flex;
    flex-direction: row;
  }
}

.main-navigation--closed.main-navigation--nojs {
  position: static;
  order: -1;
  display: block;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
  position: relative;
}
@media (min-width: 768px) {
  .nav-list {
    position: static;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    background-color: #ffffff;
  }
}
.nav-list--opened {
  background-color: #81b3d2;
  z-index: 3;
}
.nav-list::after {
  position: absolute;
  content: "";
  bottom: 0;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, transparent 0%, rgba(0, 0, 0, 0.2) 100%);
  mix-blend-mode: normal;
}
@media (min-width: 768px) {
  .nav-list::after {
    display: none;
  }
}

.nav-list__item {
  position: relative;
  display: flex;
  width: 20%;
}
@media (max-width: 768px) {
  .nav-list__item {
    box-shadow: 0 1px 0 0 rgba(1, 1, 1, 0.2), 0 1px 0 0 rgba(255, 255, 255, 0.2) inset;
  }
}

.nav-list__item:nth-child(2n):not(:nth-child(4n)) {
  margin-right: 20%;
}

.nav-list--opened .nav-list__item {
  width: 100%;
}

.nav-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 56px;
  flex-grow: 1;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
}
.nav-list__link:hover {
  color: #81b3d2;
}
.nav-list__link:active {
  color: #000000;
  opacity: 30%;
}
@media (min-width: 768px) {
  .nav-list__link {
    min-width: 153px;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
}
@media (min-width: 1200px) {
  .nav-list__link {
    min-width: 240px;
  }
}

.nav-list--opened .nav-list__link {
  color: #ffffff;
}

.nav-list__item--current .nav-list__link {
  color: #ffffff;
}
@media (min-width: 768px) {
  .nav-list__item--current .nav-list__link {
    color: #766357;
  }
}

.nav-list--opened .nav-list__item:nth-child(2n):not(:nth-child(4n)) {
  margin-right: 0;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.social__item {
  margin: 3px 6px 3px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social__item:last-child {
  margin-right: 0;
}
.social__item:active {
  background-color: #5496bd;
}

.social__link {
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #81b3d2;
  cursor: pointer;
}
.social__link:hover {
  background-color: #669ec0;
  cursor: pointer;
}
.social__link:active {
  background-color: #5496bd;
  opacity: 0.3;
}

.button {
  display: inline-block;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-align: center;
  color: #ffffff;
  background-color: #81b3d2;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
  outline: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
  cursor: pointer;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled {
  background-color: #e5e5e5;
}

.error {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 2;
}

.error__modal {
  width: 100%;
  background-color: #ffffff;
  box-shadow: 0 7px 15px 0 rgba(0, 1, 1, 0.15);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  height: 305px;
  padding: 45px 42px 35px;
}
@media (min-width: 768px) {
  .error__modal {
