  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
}

.error__button {
  width: 180px;
  height: 58px;
  margin: 0 auto;
  padding: 18px 64px;
}
@media (min-width: 768px) {
  .error__button {
    width: 272px;
    height: 58px;
    padding: 18px 107px;
  }
}

