    height: 37px;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
@media (min-width: 768px) {
  .advantages__item--first::before {
    display: none;
  }
}

@media (min-width: 768px) {
  .advantages__inner-wrapper {
