  }
}

.advantages__img--housing,
.advantages__img--food,
.advantages__img--souvenirs {
  grid-column: 1/2;
  grid-row: 1/3;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .advantages__img--housing,
.advantages__img--food,
.advantages__img--souvenirs {
    grid-column: 1/-1;
    grid-row: 1/2;
    margin: 0 auto;
  }
}

